import Vue from 'vue'
import Vuex from 'vuex'
import VueCookies from 'vue-cookies'

Vue.use(Vuex);
Vue.use(VueCookies);

export const store = new Vuex.Store({
// counter  state  
	state: {
        // 
        cr: '',

        // class 
        UCLASS_STUDENT_QUIT  : 10,
        UCLASS_STUDENT       : 19,
        UCLASS_ETC           : 20,
        UCLASS_TEACHER       : 21,
        UCLASS_COUNSELOR     : 22,
        UCLASS_VICEPRESIDENT : 28,
        UCLASS_PRESIDENT     : 29,

        // class 
        SMART_BIRTH         : 1,
        SMART_RATE          : 2,
        SMART_EVAL          : 3,

        NOTICE_ALL          : 11,
        NOTICE_NEWS         : 12,
        NOTICE_ROOM         : 13,

        HEAD_NOTICE         : 21,
        HEAD_UPDATE         : 22,
        HEAD_SERVER         : 23,

        NOTICE_RANGE_ALL    : 0,
        NOTICE_RANGE_STU    : 1,
        NOTICE_RANGE_PAR    : 2,

        // 
        uid: '',
        uname: '',
        uclass: '',
        uphone: '',
        ulogofile: '',
        is_mobile: '',

        //  
        cid: '',
        cname: '',
        now_users_cnt: '',

        //  
        is_random: '',
        is_skip: '',
        is_scoring: '',
        is_controllable: '',
	},
	getters: {
		getUclass_txt : () => (code) => {
            let text = '';
            switch (code) {
                case 10: text=''; break;
                case 19: text=''; break;
                case 20: text=''; break;
                case 21: text=''; break;
                case 22: text=''; break;
                case 28: text=''; break;
                case 29: text=''; break;
                default: text=''; break;
            }
            return text;
        },
        getUclass : () => (txt) => {
            let code = '';
            switch (txt) {
                case '': code=10; break;
                case '': code=19; break;
                case '': code=20; break;
                case '': code=21; break;
                case '': code=22; break;
                case '': code=28; break;
                case '': code=29; break;
                default: code=0; break;
            }
            return code;
        },

        getNtype_txt : () => (code) => {
            let text = '';
            switch (code) {
                case 1: text=''; break;
                case 2: text=''; break;
                case 3: text=''; break;

                case 11: text=''; break;
                case 12: text=''; break;
                case 13: text=''; break;

                case 21: text=''; break;
                case 22: text=''; break;
                case 23: text=''; break;
                default: text='Err'; break;
            }
            return text;
        },

        getNrange_txt : () => (code) => {
            let text = '';
            switch (code) {
                case 0: text=', '; break;
                case 1: text=''; break;
                case 2: text=''; break;
                default: text='Err'; break;
            }
            return text;
        },
	},
	mutations: {
        checkref (state, payload) {
            state.cr = payload.cr;
        },
        save_user_info (state, payload){

            state.uid = payload.id;
            state.uname = payload.name;
            state.is_mobile = payload.is_mobile;

            state.cid = payload.academy_id;
            state.cname = payload.cname;
            state.now_users_cnt = payload.now_users_cnt;
        },
	},
	actions : {

	}
});
